<template>
    <div class="newsinfo-container">
        <!-- 标题 -->
        <h3 class="title">{{newsInfo.title}}</h3>
        <p class="subtitle">
            <span>发表时间：{{newsInfo.add_time | dateFormat}}</span>
            <span>阅读：{{newsInfo.click}}次</span>
        </p>
        <hr>
        <!-- 内容 -->
        <div class="content" v-html="newsInfo.content"></div>
        <!-- 评论 -->
        <comment :id='this.$route.params.id'/>
    </div>
</template>
<script>
import { Toast } from 'mint-ui'
import api from '@/api/index.js'
import Comment from '@/components/subcomponents/comment'

export default {
    data() {
        return{
            newsInfo:{}
        }
    },
    created(){

    },
    mounted(){
        const id = this.$route.params.id;
        this.getnewsInfo(id)
    },
    methods:{
        getnewsInfo(id) {
            api.getnewsInfo(id).then(res => {
                if(res.data.status === 0) {
                    this.newsInfo = res.data.message[0];
                }else {
                    Toast({
                            message: '请求失败',
                            duration: 5000
                        });
                }
            })
        }
    },
    components:{
        Comment
    }
}
</script>
<style lang="scss">
.newsinfo-container{
    padding:0 4px;
    .title{
        font-size: 14px;
        text-align: center;
        margin:15px 0;
        color:red;
    }
    .subtitle{
        font-size: 13px;
        color:#226aff;
        display: flex;
        justify-content: space-between;
    }
    .content{
        img{
            width: 100%;
        }
    }
}

</style>





